import React, { useState } from 'react';
import "./PurchasedModal.scss";
import Button from '../button-component/ButtonComponent';
import Modal from "../modal-component/ModalComponent";
import Counter from "../counter/Counter";
import ProductDetailsModel from '../product-details-model/ProductDetailsModel'

class PurchasedModal extends React.Component {
	// Variable declarations
	state = {
		show: false,
		updateModal: false,
		quantity: 1
	}

	modalTitle = ""
	modalBody = ""
	modalTitleContent = ""
	modalFooterButtons = ""

	componentDidMount = () => {
		this.setModalData()
	}

	incrementQuantity = () => {
		this.setState(() => ({
			quantity: this.state.quantity + 1
		}))
	}

	decrementQuantity = () => {
		this.setState(() => ({
			quantity: this.state.quantity - 1
		}))
	}

	setModalData = () => {
		this.clearModalData()
		this.modalTitle = this.props.title
		this.setState(() => ({
			show: true,
		}))

		this.modalBody = (
			<>
				<div className="row add-gift-another-store">
					<div className="col">
						<div className="row mt-4">
							<>
								<div className="col-5 add-gift-image-select">
									<div className="mr-3">
										<ProductDetailsModel data={this.props.data} />
									</div>
								</div>
								<div className="col-7">
									<div className="ml-3">
										<div className="row col">
											<div className="col">
												<p className="font-body font-medium">Buy this gift for {this.props.stateRegistryInfo.user.firstName} {this.props.stateRegistryInfo.user.partnerUser ? 'and' + this.props.stateRegistryInfo.user.partnerUser.firstName : ''}</p>
												<p className="text-sm mt-1">Well keep the gift available in the registry until you checkout of the cart</p>
											</div>
										</div>
										<div className="col-7 mt-4">
											<div className="ml-3">
												<div className="row col">
													<div className="col">
														<p className="font-body font-medium">Great, lets update their registry</p>
														<p className="text-sm mt-1">This info will help {this.props.stateRegistryInfo.user.firstName} {this.props.stateRegistryInfo.user.partnerUser ? 'and' + this.props.stateRegistryInfo.user.partnerUser.firstName : ''} keep track of all their gifts.</p>
													</div>
												</div>
												<div className="row col mt-4">
													<div className="col m-a-input-container">
														<p className="text-sm font-medium add-gift-store-label">Name</p>
														<input
															className="w-100 mt-1 px-2 py-1"
															type='text'
															placeholder='John Doe'
														// onChange={(ev) => productNameChangeHandler(ev)}
														/>
													</div>
												</div>
												<div className="row col mt-4">
													<div className="col m-a-input-container">
														<p className="text-sm font-medium add-gift-store-label">Email Address</p>
														<input
															className="w-100 mt-1 px-2 py-1"
															type='text'
															placeholder='johndoe@email.com'
														// onChange={(ev) => productNameChangeHandler(ev)}
														/>
													</div>
												</div>
												<div className="row col mt-4">
													<div className="col m-a-input-container">
														<p className="text-sm font-medium add-gift-store-label">Quantity</p>
														<Counter
															className="mt-2"
															counter={this.state.quantity}
															incrementHandler={this.incrementQuantity}
															decrementHandler={this.decrementQuantity}
														/>
													</div>
												</div>
												<div className="row col mt-4">
													<div className="col m-a-input-container">
														<p className="text-sm font-medium add-gift-store-label">Store Name</p>
														<input
															className="w-100 mt-1 px-2 py-1"
															type='text'
															placeholder='Eg. Target'
														// onChange={(ev) => productNameChangeHandler(ev)}
														/>
													</div>
													<div className="col m-a-input-container ml-3">
														<p className="text-sm font-medium add-gift-store-label">Order Number</p>
														<input
															className="w-100 mt-1 px-2 py-1"
															type='text'
															placeholder='0012345'
														// onChange={(ev) => productNameChangeHandler(ev)}
														/>
													</div>
												</div>
												<div className="row col mt-5">
													<div className="col m-a-input-container">
														<p className="text-sm font-medium add-gift-store-label">Gift Message</p>
														<textarea className="w-100 mt-1 text-body p-1"
															rows="2"
															placeholder="Write a message for gift receiver"
														// onChange={(ev) => noteChangeHandler(ev)}
														/>
													</div>
												</div>
											</div>
										</div>
									</div>
								</div>
							</>
						</div>
					</div>
				</div>
			</>
		)
	}

	clearModalData = () => {
		this.modalTitle = ""
		this.modalBody = ""
		this.modalTitleContent = ""
		this.modalFooterButtons = ""
	}

	modalCloseHandler = () => {
		// this.clearModalData()
		this.setState({
			show: false
		})
	}


	render() {
		return (
			<>
				<Modal
					show={this.props.show}
					updateModal={this.state.updateModal}
					title={this.props.title}
					titleContent={this.modalTitleContent}
					footer={this.modalFooterButtons}
					closeModal={this.props.close}>
					{this.modalBody}
				</Modal>
			</>
		)
	}
}

// const mapStateToProps = state => {
// 	return {
// 		stateRegistryInfo: state.registry.info
// 	}
// }

export default PurchasedModal;